import React, { useState } from "react";

const Cart = ({ getDataById }) => {
  const [cartItems, setCartItems] = useState([]);

  const onDrop = e => {
    e.preventDefault();
    const itemId = e.dataTransfer.getData("text/plain");
    const itemFound = cartItems.find(item => item.id === Number(itemId));
    let newState;
    if (itemFound) {
      newState = modifyPrice(itemFound.id);
    } else {
      const newItem = getDataById(itemId);
      newState = [...cartItems, newItem];
    }
    setCartItems(newState);
  };

  const onDragOver = e => {
    e.preventDefault();
  };

  const modifyPrice = id => {
    return cartItems.map(item => {
      if (item.id === Number(id)) {
        item.price += item.price;
      }
      return item;
    });
  };

  const calculateTotal = () => {
    const total = cartItems.reduce((acc, curr) => {
      return acc + curr.price;
    }, 0);
    return total.toFixed(2);
  };

  return (
    <div
      className="Cart"
      onDrop={e => onDrop(e)}
      onDragOver={e => onDragOver(e)}
    >
      <div className="Cart__header">Cart</div>
      <div className="Cart__items">
        {cartItems.map(({ name, price, id }) => {
          return (
            <div className="Cart__item" key={`cartItem-${id}`}>
              <span className="Cart__item--name">{name}</span>
              <span className="Cart__item--price">{price}$</span>
            </div>
          );
        })}
        <div className="Cart__footer">
          <span className="Cart__footer--description">Total: </span>
          <span className="Cart__footer--price">{calculateTotal()}$</span>
        </div>
      </div>
    </div>
  );
};
export default Cart;
