import React from "react";
import { groceries } from "api/data";

export const DataContext = React.createContext(groceries);
export const DataProvider = props => {
  const getDataById = id => groceries.find(item => item.id === Number(id));
  const getData = () => groceries;

  return (
    <DataContext.Provider
      value={{
        getDataById,
        getData
      }}
    >
      {props.children}
    </DataContext.Provider>
  );
};
