import React from "react";

const Shop = ({ getData }) => {
  const onDragStart = (e, id) => {
    e.dataTransfer.setData("text/plain", id);
  };
  return (
    <div className="Shop">
      {getData().map(({ name, category, price, id }) => {
        return (
          <div
            className="ShopItem"
            key={id}
            draggable
            onDragStart={e => onDragStart(e, id)}
          >
            <h4 className="ShopItem__title">{name}</h4>
            <h5 className="ShopItem__category">{category}</h5>
            <h2 className="ShopItem__price">{price}$</h2>
          </div>
        );
      })}
    </div>
  );
};
export default Shop;
